<h1 id="laravel" tabindex="-1"><a class="header-anchor" href="#laravel" aria-hidden="true">#</a> Laravel</h1>
<h2 id="description" tabindex="-1"><a class="header-anchor" href="#description" aria-hidden="true">#</a> Description</h2>
<p>Laravel est un framework PHP créé par Taylor Otwell.</p>
<h2 id="prerequis" tabindex="-1"><a class="header-anchor" href="#prerequis" aria-hidden="true">#</a> Prérequis</h2>
<ul>
<li><a href="https://www.php.net/downloads" target="_blank" rel="noopener noreferrer">PHP<OutboundLink/></a></li>
<li><a href="https://getcomposer.org/download/" target="_blank" rel="noopener noreferrer">Composer<OutboundLink/></a> (Composer est un gestionaire de dépendance)</li>
<li><a href="https://code.visualstudio.com/download" target="_blank" rel="noopener noreferrer">Visual Studio code<OutboundLink/></a> (Est un éditeur de texte)</li>
<li><a href="https://laragon.org/download/" target="_blank" rel="noopener noreferrer">Laragon<OutboundLink/></a> (Serveur http, de base de donnée très performant)</li>
</ul>
<h2 id="installation" tabindex="-1"><a class="header-anchor" href="#installation" aria-hidden="true">#</a> Installation</h2>
<ol>
<li>Ouvrir un terminal</li>
<li>Lancer la commande suivante :</li>
</ol>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">composer</span> create-project --prefer-dist laravel/laravel mon_projet
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><ol start="3">
<li>Entrer dans le dossier du projet</li>
</ol>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>  <span class="token builtin class-name">cd</span> mon_projet
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><ol start="4">
<li>Lancer le projet</li>
</ol>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>php artisan serve
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h2 id="outils" tabindex="-1"><a class="header-anchor" href="#outils" aria-hidden="true">#</a> Outils</h2>
<h3 id="modelisation-de-la-base-de-donnee" tabindex="-1"><a class="header-anchor" href="#modelisation-de-la-base-de-donnee" aria-hidden="true">#</a> Modélisation de la base de donnée</h3>
<p>Pour modélier votre base de donnée vous pouvez utilier <a href="https://www.laravelsd.com/" target="_blank" rel="noopener noreferrer">Laravel Schema Designer<OutboundLink/></a> pour le faire plus facilement. En plus ce site vous permet:</p>
<ul>
<li>de sauvegarder vos modèles de données</li>
<li>de partager vos modèles de données</li>
<li>de générer les controlleurs, les modèles et les migrations</li>
</ul>
<h3 id="administration" tabindex="-1"><a class="header-anchor" href="#administration" aria-hidden="true">#</a> Administration</h3>
<h4 id="infyom-laravel-generator" tabindex="-1"><a class="header-anchor" href="#infyom-laravel-generator" aria-hidden="true">#</a> Infyom Laravel Generator</h4>
<p>C'est un puissant outil qui permet de générer très rapidement des CRUDs, des APIs, des tests unitaires et une documentation avec Swagger.
Il vous permet de gérer tout cela avec un backoffice qui utilise le theme CoreUI ou adminlte2.</p>
<p>Le répo git:</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">git</span> clone https://github.com/InfyOmLabs/coreui-generator.git mon_projet
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p><a href="https://labs.infyom.com" target="_blank" rel="noopener noreferrer">Site<OutboundLink/></a></p>
<h3 id="front-end-vue-js" tabindex="-1"><a class="header-anchor" href="#front-end-vue-js" aria-hidden="true">#</a> Front End Vue JS</h3>
<p>Le front end d'une application laravel peut être faite avec du blade ou avec Vue JS. L'avantage d'utiliser VueJS c'est que l'on peut créer des Single Page Applications (SPA) qui sont plus rapides et plus dynamiques.</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">composer</span> require laravel/ui
php artisan ui vue
<span class="token function">npm</span> 

</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br></div></div><h2 id="librairies-a-tester" tabindex="-1"><a class="header-anchor" href="#librairies-a-tester" aria-hidden="true">#</a> Librairies à tester</h2>
